@charset "UTF-8";
/* ================================
	Frontend CSS.

	Index:
	r01 - Placeholders
	r02 - Global
	r03 - Utilities
	r04 - Components
	r05 - Layouts
===================================*/
.wrapper {
  overflow: hidden;
}

.rt-d-flex {
  display: flex;
}

.rt-flex-wrap {
  flex-wrap: wrap;
}

.rt-justify-content-center {
  justify-content: center;
}

.rt-align-items-center {
  align-items: center;
}

.rt-absolute-content, .rt-relative-content, .rt-static-content {
  position: relative;
}

.rt-primary-color {
  color: var(--color-primary);
}

.rt-primary-color-bg {
  background-color: var(--color-primary);
}

.rt-white-color {
  color: var(--color-white);
}

.rt-white-color-bg {
  background-color: var(--color-white);
}

.rt-black-color {
  color: var(--color-black);
}

.rt-black-color-bg {
  background-color: var(--color-black);
}

.rt-mb-0 {
  margin-bottom: 0;
}

/**
 * Global CSS.
 */
:root {
  --color-primary: #0F4E41;
  --color-green-dark: #082B24;
  --color-green-100: #B5C8C4;
  --color-green-200: #91AEA8;
  --color-green-300: #5E8880;
  --color-green-400: #3F7167;
  --color-green-600: #0E473B;
  --color-green-700: #0B372E;
  --color-green-800: #6F7C7A;
  --color-green-900: #0c312b;
  --color-dark-80: #333;
  --color-dark-60: #666;
  --color-dark-70: #999;
  --color-dark-10: #e5e5e5;
  --color-white: #fff;
  --color-black: #001703;
  --color-body: #808080;
  --color-gray: #EFFAF8;
  --color-paragraph: #6F7C7A;
  --color-secondary: #C3F831;
  --color-background:#F1FCFA;
  --color-background2:#EEFAF8;
  --color-background3:#F2F8F9;
  --font-body: "Inter", sans-serif;
  --font-title: "Playfair Display", serif;
  --border-radius-xs: 4px;
  --border-radius-sm: 6px;
  --border-radius-md: 8px;
  --border-radius-lg: 16px;
  --border-radius-xl: 20px;
}

/* ===========================
// Typography
==============================*/
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: 16px;
  color: var(--color-body);
  font-weight: 400;
}

p {
  font-family: var(--color-paragraph);
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  color: var(--color-paragraph);
  margin-bottom: 15px;
}

/*==== heading fonts setup ====*/
h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--color-black);
  font-family: var(--font-title);
  line-height: 1.5;
  font-weight: 500;
}

h1 {
  font-size: 48px;
  line-height: 1.23;
}
@media (max-width: 767px) {
  h1 {
    font-size: 36px;
  }
}

h2 {
  font-size: 40px;
}

h3 {
  font-size: 32px;
}

h4 {
  font-size: 26px;
}

h5 {
  font-size: 22px;
}

h6 {
  font-size: 18px;
}

