/*==== text color & bg color ====*/
/*============================ 
// Global styles
=============================*/
html {
  overflow-x: hidden !important;
  width: 100%;
  height: 100%;
  text-rendering: optimizeLegibility;
  scroll-behavior: smooth;
  vertical-align: baseline;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

main {
  position: relative;
  background-color: var(--color-white);
  transition: all 0.3s ease-in-out;
  z-index: 2;
}

ul,
li {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: var(--color-primary);
  display: inline-block;
  transition: all 0.3s ease-in-out;
}
a:link, a:visited, a:focus {
  text-decoration: none;
  outline: 0;
}
a:hover {
  text-decoration: none;
  color: var(--color-primary);
}

img {
  max-width: 100%;
  height: auto;
}

input:focus,
textarea:focus,
select:focus {
  outline: 0;
  box-shadow: none;
}

.no-padding {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.no-padding__left {
  padding-left: 0 !important;
}
.no-padding__right {
  padding-right: 0 !important;
}

.section-padding {
  padding-block: 120px;
}
@media (max-width: 1199px) {
  .section-padding {
    padding: 100px 0;
  }
}
@media (max-width: 991px) {
  .section-padding {
    padding: 80px 0;
  }
}

section,
.section {
  position: relative;
}

.wow {
  visibility: hidden;
}

@media (min-width: 1400px) {
  .container,
.container-lg,
.container-md,
.container-sm,
.container-xl {
    max-width: 1170px;
  }
  .container-sm {
    max-width: 880px;
  }
}
/**
 * Utility CSS.
 */
/*======================================
// pre-loader
=====================================*/
#preloader {
  background: url("../../media/preloader.gif") rgba(255, 255, 255, 0.99) no-repeat scroll center center;
  height: 100%;
  left: 0;
  overflow: visible;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999999;
}

/*======================================
// Back-to-top
=====================================*/
#back-to-top {
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  z-index: 99;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
}
#back-to-top .round-bc-top {
  width: 72px;
  height: 72px;
  border-radius: 72px;
  background-color: rgba(181, 200, 196, 0.2);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
}
#back-to-top .round-bc-top svg {
  color: var(--color-primary);
}
#back-to-top .round-img {
  background-color: var(--color-white);
  width: 120px;
  height: 120px;
  border-radius: 120px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}
#back-to-top .round-img img {
  animation-duration: 10s;
}

.mb--24 {
  margin-bottom: 24px !important;
}

.mb--40 {
  margin-bottom: 40px !important;
}

.mt--24 {
  margin-top: 24px !important;
}

.mt--18 {
  margin-top: 18px !important;
}

.mt--60 {
  margin-top: 60px !important;
}

/**
 * Components: About.
 */
.propella-img-wrapper .item-img {
  border-radius: 12px;
}
.propella-img-wrapper .item-img img {
  border-radius: 12px;
}
.propella-img-wrapper .item-img .small-img {
  position: absolute;
  bottom: -35px;
  right: 40px;
  border-radius: 12px;
  background-color: var(--color-white);
  box-shadow: -16px 16px 64px 0px rgba(0, 9, 7, 0.12);
  padding: 10px;
}
.propella-img-wrapper .item-img .small-img img {
  border-radius: 12px;
}
.propella-img-wrapper .item-img .play-area {
  position: absolute;
  bottom: 20px;
  left: 54px;
  height: 110px;
  width: 280px;
  background-color: var(--color-primary);
  border-radius: 500px 0px 0px 500px;
  z-index: 2;
}
.propella-img-wrapper .item-img .play-area .about-play {
  padding: 20px;
  position: relative;
  z-index: 2;
}
.propella-img-wrapper .item-img .play-area .about-play::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  right: 0px;
  bottom: -19px;
  border-top: 21px solid #082B24;
  border-right: 25px solid transparent;
}
.propella-img-wrapper .item-img .play-area .text {
  color: #E7EDEC;
  font-size: 24px;
  font-weight: 600;
  font-family: var(--font-title);
  margin-left: 20px;
}
.propella-img-wrapper .dot-shape ul li {
  position: absolute;
  z-index: -1;
  animation: move 10s infinite linear;
}
.propella-img-wrapper .dot-shape ul li:nth-child(1) {
  bottom: -60px;
  right: 20px;
}
.propella-img-wrapper .dot-shape ul li:nth-child(2) {
  top: -20px;
  right: 150px;
}
.propella-img-wrapper .about-counter {
  position: absolute;
  right: 40px;
  top: 30px;
  transform: rotate(-90deg);
}
.propella-img-wrapper .about-counter .exprience-number {
  color: var(--color-dark-80);
  font-size: 36px;
  font-weight: 800;
  line-height: 1;
  text-transform: uppercase;
}
.propella-img-wrapper .about-counter .exprience-number .label {
  font-size: 14px;
  font-weight: 400;
  color: var(--color-dark-70);
}

.about-list {
  margin-bottom: 30px;
}
.about-list ul li {
  color: var(--color-black);
  font-size: 18px;
  line-height: 40px;
}
.about-list ul li svg {
  margin-right: 10px;
}
@media screen and (max-width: 760px) {
  .about-list ul {
    justify-content: center;
  }
}

.item-social ul {
  gap: 12px;
  justify-content: end;
}
.item-social ul li a {
  color: var(--color-white);
  height: 40px;
  width: 40px;
  border: 1px solid #3F7167;
  border-radius: 50px;
  background-color: transparent;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease-in-out;
}
.item-social ul li a:hover {
  background-color: var(--color-white);
  color: var(--color-black);
  border: 1px solid transparent;
}
@media only screen and (max-width: 1200px) {
  .item-social ul {
    justify-content: start;
  }
}

.item-social-2 ul {
  gap: 20px;
}
.item-social-2 ul li a {
  transition: all 0.3s ease-in-out;
}
.item-social-2 ul li a:hover svg {
  color: var(--color-primary);
}
.item-social-2 ul li a svg {
  color: #4d4d4d;
}

/**
 * Components: Input.
 */
.input-group {
  width: 100%;
  position: relative;
}
.input-group::before {
  content: url(../../media/shape/mail.svg);
  position: absolute;
  top: 16px;
  left: 15px;
  z-index: 4;
}
.input-group .form-control {
  background-color: var(--color-green-700);
  border: 0;
  border-radius: 8px !important;
  height: 48px;
  width: 100%;
  font-size: 16px;
  line-height: 28px;
  color: var(--color-white);
  padding: 0px 0px 0px 40px;
}
.input-group .form-control:focus {
  outline: 0;
  box-shadow: none;
}
.input-group .form-control::placeholder {
  color: var(--color-green-300);
}
.input-group .btn-outline-secondary {
  border: 0;
  color: var(--color-primary);
  background-color: var(--color-green-400);
  border-radius: 8px !important;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 38px;
  width: 38px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 5px;
  transition: all 0.4s ease-in-out;
  z-index: 4;
}
.input-group .btn-outline-secondary:hover {
  background-color: var(--color-primary);
  color: var(--color-black);
}
.input-group .btn-outline-secondary:hover img {
  animation: headShake 1.5s;
  color: var(--color-black);
}
.input-group .btn-outline-secondary img {
  transition: all 0.4s ease-in-out;
}
.input-group .btn-outline-secondary:focus {
  outline: 0;
  box-shadow: none;
}

.propella-brand-wrap {
  background-color: var(--color-green-700);
}
.propella-brand-wrap--style1 {
  background-color: var(--color-white);
}
.propella-brand-wrap--style-2 {
  padding: 0px 0px 85px 0px;
}
@media only screen and (max-width: 1200px) {
  .propella-brand-wrap--style-2 {
    padding: 0px 0px 50px 0px;
  }
}
.propella-brand-wrap--style-3 {
  padding: 70px 0px 70px 0px;
}
.propella-brand-wrap .brand-heading {
  color: var(--color-black);
  text-align: center;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 26px;
  margin-bottom: 42px;
}
.propella-brand-wrap .bran-wrapper {
  border-bottom: 2px solid #E5E5E5;
}
.propella-brand-wrap .bran-wrapper--style-2 {
  border: none;
}
@media only screen and (max-width: 1200px) {
  .propella-brand-wrap .bran-wrapper {
    border: none;
  }
}

.brand {
  text-align: center;
}
.brand img {
  filter: grayscale(100%);
  opacity: 20%;
  transition: all 0.4s ease-in-out;
  cursor: pointer;
}
.brand img:hover {
  filter: grayscale(0);
  opacity: 100%;
}
.brand--style-2 {
  margin-bottom: 32px;
}
@media only screen and (max-width: 1200px) {
  .brand {
    margin-bottom: 30px;
  }
}

/**
 * Components: Blog.
 */
.propella-blog-box {
  border: 1px solid var(--color-dark-10);
  border-radius: var(--border-radius-lg);
  transition: all 0.3s ease-in-out;
  padding: 15px;
  margin-bottom: 28px;
}
.propella-blog-box:hover .item-img a img {
  transform: scale(1.17);
}
.propella-blog-box .item-img {
  flex-shrink: 0;
  border-radius: 12px;
}
.propella-blog-box .item-img a {
  border-radius: 12px;
}
.propella-blog-box .item-img a img {
  transition: all 500ms ease-in-out;
  border-radius: 12px;
}
.propella-blog-box .content {
  padding: 15px;
}
.propella-blog-box .content .entry-meta {
  gap: 34px;
  color: var(--color-dark-60);
  letter-spacing: -0.28px;
  line-height: 32px;
  margin-bottom: 4px;
}
.propella-blog-box .content .entry-meta a {
  color: var(--color-dark-70);
  transition: all 0.3s ease-in-out;
  letter-spacing: -0.28px;
  line-height: 32px;
}
.propella-blog-box .content .entry-meta a:hover {
  color: var(--color-black);
}
.propella-blog-box .content .entry-meta .author-name {
  color: var(--color-black);
  font-family: var(--font-body);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.28px;
}
.propella-blog-box .content .entry-meta .data-information {
  gap: 6px;
}
.propella-blog-box .content .entry-meta .reading-time {
  gap: 8px;
  color: var(--color-black);
  font-family: var(--font-body);
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.28px;
}
.propella-blog-box .content .title .blog-card-title {
  color: var(--color-black);
  font-family: var(--font-title);
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  letter-spacing: -0.48px;
}
.propella-blog-box .content .title .blog-card-title--style2 {
  font-weight: 700;
  padding: 0px 20px 0px 0px;
}
.propella-blog-box .content .title .blog-card-title--style2:hover {
  color: var(--color-primary);
}
.propella-blog-box .content .propella-button .propella-btn.style-2 {
  border: 1px solid var(--color-dark-10);
  padding: 11px 20px;
  color: var(--color-body);
}
.propella-blog-box .content .propella-button .propella-btn.style-2:hover {
  color: var(--color-white);
}
.propella-blog-box .content .propella-button .propella-btn.style-3 {
  border: 1px solid var(--color-dark-10);
  padding: 11px 20px;
  color: var(--color-black);
  font-family: var(--font-body);
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;
}
.propella-blog-box .content .propella-button .propella-btn.style-3:hover {
  color: var(--color-white);
  background-color: var(--color-primary);
  font-weight: 400;
}
.propella-blog-box.style-2 {
  border: 0;
  border-radius: 0;
  padding: 0;
}
.propella-blog-box.style-2 .content {
  padding: 0;
  padding-top: 30px;
}
.propella-blog-box.style-2 .content .badge {
  background-color: transparent;
  border: 1px solid var(--color-green-100);
  color: var(--color-primary);
  border-radius: var(--border-radius-xs);
  margin-bottom: 15px;
  transition: all 0.3s ease-in-out;
}
.propella-blog-box.style-2 .content .badge:hover {
  background-color: var(--color-primary);
}
.propella-blog-box.style-2 .content .badge:hover a {
  color: var(--color-white);
}
.propella-blog-box.style-2 .content .title a {
  font-weight: 600;
}
.propella-blog-box--style2 {
  gap: 15px;
}
@media screen and (max-width: 768px) {
  .propella-blog-box {
    flex-direction: column;
  }
}

.title {
  font-family: var(--font-title);
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.48px;
  line-height: 34px;
}
.title a {
  color: var(--color-black);
}
.title .style-1 {
  font-size: 24px;
  font-weight: 400;
  letter-spacing: -0.48px;
  color: var(--color-black);
  transition: all 0.3s ease-in-out;
  display: inline;
  padding-bottom: 0;
  -webkit-transition: background-size 0.8s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: background-size 0.8s cubic-bezier(0.25, 0.8, 0.25, 1);
  background-image: -webkit-gradient(linear, left top, left bottom, from(var(--color-primary)), color-stop(98%, var(--color-primary)));
  background-image: linear-gradient(to bottom, var(--color-primary), var(--color-primary));
  background-size: 0 1px;
  background-repeat: no-repeat;
  background-position: left 100%;
}
.title .style-1:hover {
  color: var(--color-primary);
  background-size: 100% 1px;
}
.title .style-2 {
  color: var(--color-black);
  font-family: var(--font-title);
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 32px;
  letter-spacing: -0.48px;
  transition: all 0.3s ease-in-out;
  display: inline;
  padding-bottom: 0;
  -webkit-transition: background-size 0.8s cubic-bezier(0.25, 0.8, 0.25, 1);
  transition: background-size 0.8s cubic-bezier(0.25, 0.8, 0.25, 1);
  background-image: -webkit-gradient(linear, left top, left bottom, from(var(--color-primary)), color-stop(98%, var(--color-primary)));
  background-image: linear-gradient(to bottom, var(--color-primary), var(--color-primary));
  background-size: 0 1px;
  background-repeat: no-repeat;
  background-position: left 100%;
}
.title .style-2:hover {
  color: var(--color-primary);
  background-size: 100% 1px;
}

.calendar-tag {
  font-size: 14px;
  color: var(--color-dark-60);
  border-radius: var(--border-radius-md);
  background-color: var(--color-white);
  padding: 10px;
  position: absolute;
  top: 8px;
  right: 8px;
  line-height: 1;
}
.calendar-tag img {
  margin-right: 8px;
}
.calendar-tag--style2 {
