}
.rt-header-action .header-logo::after {
  content: "";
  height: 92px;
  width: 1px;
  background-color: #d9d9d9;
  position: absolute;
  top: -28px;
  left: -30px;
  z-index: 1;
}
.rt-header-action .header-logo.style-2::after {
  background-color: #b4b4b4;
}

.kariez-hamburger {
