  z-index: 1;
}
.kariez-hamburger::after {
  content: "";
  height: 90px;
  width: 100px;
  background-color: var(--color-primary);
  position: absolute;
  top: -26px;
  left: -30px;
  scale: 0;
  transition: all 0.5s;
  z-index: -1;
}
.kariez-hamburger:hover .line:nth-child(1) {
