    font-size: 16px;
  }
}
.rt-main-menu .menu .menu-item .menu-link svg {
  transition: all 0.3s ease-in-out;
}
.rt-main-menu .menu .menu-item .menu-link:hover {
  color: var(--color-white);
}
.rt-main-menu .menu .menu-item .menu-link:hover svg {
  fill: var(--color-primary);
}
.rt-main-menu .menu .menu-item .menu-link:hover::before {
  opacity: 1;
  width: 100%;
}
.rt-main-menu .menu .menu-item .menu-link.active2 {
  color: var(--color-primary);
}
.rt-main-menu .menu .menu-item .menu-link.active2::before {
  height: 2px;
  width: 100%;
  content: "";
  background-color: var(--color-white);
  position: absolute;
  bottom: -2px;
  left: 50%;
  transition: all 0.4s ease-in-out;
  opacity: 1;
}
.rt-main-menu .menu .menu-item .menu-link--style2 {
  color: var(--color-black);
  position: relative;
  padding: 33px 0px 33px;
}
.rt-main-menu .menu .menu-item .menu-link--style2 .rt-dot {
  display: flex;
  gap: 5px;
  position: absolute;
  top: 61px;
  opacity: 0;
  transition: all 0.5s ease-in-out;
  visibility: hidden;
}
.rt-main-menu .menu .menu-item .menu-link--style2 .rt-dot li {
  height: 4px;
  width: 4px;
  border-radius: 50%;
}
.rt-main-menu .menu .menu-item .menu-link--style2 .rt-dot li:nth-child(1) {
  background-color: var(--color-primary);
  transition-delay: 0.1s;
}
.rt-main-menu .menu .menu-item .menu-link--style2 .rt-dot li:nth-child(2) {
  background-color: #358071;
  transition-delay: 0.2s;
}
.rt-main-menu .menu .menu-item .menu-link--style2 .rt-dot li:nth-child(3) {
  background-color: #7bb9ad;
  transition-delay: 0.3s;
}
.rt-main-menu .menu .menu-item .menu-link--style2:hover {
  color: var(--color-primary);
}
.rt-main-menu .menu .menu-item .menu-link--style2:hover .rt-dot {
  opacity: 1;
  visibility: visible;
}
.rt-main-menu .menu .menu-item .menu-link--style-3 {
  padding: 9px 0px;
  color: var(--color-black);
  position: relative;
}
.rt-main-menu .menu .menu-item .menu-link--style-3:hover {
  color: var(--color-primary);
}
.rt-main-menu .menu--style-2 {
  flex-direction: column;
  justify-content: start;
  display: flow-root;
  -webkit-box-align: start;
}

.rt-main-menu .menu .menu-item:hover.menu-item-has-children::after {
  border-color: var(--color-primary);
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu {
  position: absolute;
  top: 100%;
  background: var(--color-white);
  min-width: 260px;
  padding: 15px 12px 13px 12px;
  list-style: none;
  margin: 0;
  box-shadow: 0 13px 48px 0 rgba(0, 0, 0, 0.15);
  visibility: hidden;
  opacity: 0;
  z-index: 9;
  transform-origin: top center;
  transform: scaleY(0) translateZ(100px);
  transition: opacity 500ms ease, visibility 500ms ease, transform 700ms ease;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu .menu-item {
  margin-bottom: 0px;
  opacity: 0;
  transition: all 0.4s;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu a {
  font-size: 15px;
  padding: 6px 17px;
  padding-left: 0;
  color: var(--color-black);
  font-weight: 500;
  overflow: hidden;
  position: relative;
  z-index: 1;
  display: flex;
  line-height: 30px;
  transition: padding 0.3s;
}
.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu a:hover {
  padding-left: 12px;
  color: var(--color-primary);
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu a::before {
  color: var(--color-primary);
  content: "\f105";
  font-family: "Font Awesome 6 Free";
  font-weight: 600;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: -20px;
  transition: left 0.2s cubic-bezier(0.46, 0.51, 0.62, 0.92);
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu a:hover::before {
  left: 0;
}

.rt-main-menu .menu .menu-item.menu-item-has-children .sub-menu a:hover::after {
  width: 100%;
}

.rt-main-menu .menu .menu-item:hover > .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: scaleY(1) translateZ(0px);
}

.rt-main-menu .menu .menu-item:hover > .sub-menu .menu-item {
  opacity: 1;
}

.rt-main-menu .menu > .menu-item {
  margin-right: 25px;
}
@media only screen and (min-width: 1200px) {
  .rt-main-menu .menu > .menu-item {
    margin-right: 35px;
  }
}

.rt-main-menu .menu > .menu-item > a {
  padding: 36px 0px 36px;
}

.rt-main-menu .menu > .menu-item:last-child {
  margin-right: 0;
}

/*rt-main-menu style-2*/
.rt-main-menu.style-2 .menu .menu-item {
  position: relative;
}
.rt-main-menu.style-2 .menu .menu-item > a {
  color: var(--color-black);
}

